
import {Link } from "react-router-dom";
import React from 'react';
import { Hexagon, HomeIcon, CompassIcon, PortfolioIcon, WorkspaceIcon, HamburgerMenu } from "./icons.js";
import "./navigation.css"


class Badges extends React.Component {
    constructor(props) {
      super(props);
      window.addEventListener("resize", this.hideBadges)
      this.state = { show: (window.innerWidth > 750) };
    }
    hideBadges = () => {

      this.setState((props, state) => {
        return { show: (window.innerWidth > 750) }
      })
    }
    render() {
      if (this.state.show) {
        return (<div className="badge-div">
          <h1 className="badge-text">Badges</h1>
          <Hexagon color="#EECC7C" />
          <Hexagon color="#7CA9EE" />
          <Hexagon color="#8BEE7C" />
          <Hexagon color="#EE7C7C" />
          <Hexagon color="white" />
        </div>)
      }
      else {
        return null;
      }
    }
  }


export class Navbar extends React.Component {
    render() {
      return (
        <div className="navbar-base">
          <div className="navbar-internal-div">

            <div onClick={this.props.sideNavAnimation} style={{ display: "inline-block" }}><HamburgerMenu /></div>
            <Link to="/"><HomeIcon /></Link>
            <h1 className="navbar-text">{this.props["pageName"]}</h1>
            <Badges />
          </div>
        </div>
      );
    }
  }


export class SideNav extends React.Component {
    render() {
      var sideNavIconSize = "25";
      return (<div className={this.props.isExtended}>
        <div className="sidenav">
          <div className="sidenav-internal-div">
            <Link to="/journey">
            <div>
            <CompassIcon size={sideNavIconSize} />
            <p1 className="sidenav-text">Your Journey</p1>
            </div>
            </Link>
            <Link to="/portfolio" >
            <div >
              <PortfolioIcon size={sideNavIconSize} />
              <p1 className="sidenav-text">Your Portfolio</p1>
            </div>
            </Link>
            <Link to="/workspace">
              <div>
                <WorkspaceIcon size={sideNavIconSize} />
                <p1 className="sidenav-text">Your Workspace</p1>
              </div></Link>

          </div>

        </div>
      </div>)
    }
  }
