import axios from "axios";
export var user = {
    username: "",
    toDoList: [[],[],[],[]],
    cash: 0,
    xp: 0,
}
var currentUser = {
    username: "",
};
var loggedIn = true;
function makeToDoList(lst, offset = 0) {
    return (lst.map((content, index) => ({
        id: `${index} ${offset}`,
        content: content
    })))
}
export async function getRequestToDoList() {
    var todo_list;
    await axios.get("https://cambio-api.herokuapp.com/api/students/1/").then(
        response => {
            todo_list = [makeToDoList(response.data.backlog),
            makeToDoList(response.data.to_do, 1000), makeToDoList(response.data.doing, 2000),
           makeToDoList(response.data.done,3000)];
            user.cash = response.data.cash;
            user.xp = response.data.xp;
        }
    )
    user.toDoList = todo_list
    return todo_list

}
var downloadData
export async function loadDownloadData() {
    var downloadData1;
        await axios.get("https://cambio-api.herokuapp.com/api/downloads/1/").then(
            response => {downloadData1 = response.data
                downloadData = response.data}
        )
    return downloadData1;
}

export function fetchChallenges() {
    return downloadData.challenges
} 

export function fetchToDoList() {
    //returns a list with 4 lists that contain strings
    //return [[],[],[],[]];
    return user.toDoList

    
}

export function updateToDoList(list) {
    user.toDoList = list;
    var tempList = [];
    list.forEach(lst => {
        var tempLst = [];
        lst.forEach(item => {
            tempLst.push(item.content)
        })
        tempList.push(tempLst)
    })
    
    axios.patch("https://cambio-api.herokuapp.com/api/students/1/", ({
        backlog: tempList[0],
        to_do: tempList[1],
        doing: tempList[2],
        done: tempList[3],
    }))
    //returns nothing
}

export function isLoggedIn() {
    return loggedIn;
}
export function login(username, password) {
    currentUser.username = username;
    //return boolean
    if ((currentUser.username === user.username)){
        loggedIn = true;
        return true;
    }
    return false;

}

export function getVideoURL() {
    return downloadData.video_url;
}

export function fetchDownloadLink() {
    return downloadData.download_url;
}


