import React from 'react';
import "./style.css"
import "./backend.js";
import {downloadData, getVideoURL, isLoggedIn, login, fetchDownloadLink, getRequestToDoList, fetchChallenges, user, loadDownloadData} from './backend.js';
import {SideNav, Navbar} from "./reused-components/navigation.js";
import { Redirect, Link } from "react-router-dom";
import axios from 'axios';


class CheckIcon extends React.Component {
  render() {
    return (<svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 0 24 24" width="24px" fill="#000000"><path d="M0 0h24v24H0z" fill="none"/><path d="M9 16.17L4.83 12l-1.42 1.41L9 19 21 7l-1.41-1.41z"/></svg>)
  }
}

  class MasterButton extends React.Component {
    render() {
      var size = this.props.size;
      
      return (
        <div>
  
      <div style={{borderRadius:"2vh", width: "auto", height: "auto", paddingRight: "1.5vw", paddingLeft: "1.5vw", paddingTop: "3px",
      paddingBottom: "0.4vh", backgroundColor: "#965594"}}>
        <h1 style={{fontSize: "2vh", fontFamily: "SegoeUI-Semibold, Segoe UI", fontWeight:"600", color: "white"}}>{this.props.text}</h1>
      </div>
        <div style={{justifyContent: "center", display: "flex"}}><LoadingBar completion={this.props.loadingBarCompletion}/></div>
          

        </div>
      
  
      )
    }
  }

  
class LoadingBar extends React.Component {
  render() {
    //var lengthLoadingBar = this.props.length;
    var color = ["none", "none", "none"];
    for (var i = 0; i < this.props.completion; i++) {
      color[i] = "#8bee7c";
    }
    return (
      <div style={{position: "absolute", top: "calc(7vh)"}}>
      <svg xmlns="http://www.w3.org/2000/svg" width="auto" height="1vh" viewBox="0 0 112.065 10.945">
        <g id="Group_21" data-name="Group 21" transform="translate(0 0)">
          <rect id="Rectangle_1510" data-name="Rectangle 1510" width={110.009} height="10.945" rx="5.472" transform="translate(1.028 0)" fill="#fff"/>
          <rect id="Rectangle_1511" data-name="Rectangle 1511" width={49.35} height="10.945" rx="5.472" transform="translate(0 0)" fill={color[0]}/>
          <rect id="Rectangle_1512" data-name="Rectangle 1512" width={49.35} height="10.945" transform="translate(34.956 0)" fill={color[1]}/>
          <rect id="Rectangle_1513" data-name="Rectangle 1513" width={111.037} height="10.945" rx="5.472" transform="translate(1.028 0)" fill={color[2]}/>
        </g>
      </svg></div>
    );
  }
}


  
  export class HomePage extends React.Component {
    constructor(props) {
      super(props)
      this.state = { isExtended: "extended", popUp: false}
      //getRequestToDoList()
      this.refreshDownloadData();
    }
    refreshDownloadData = async () => {
      var downloadData = await loadDownloadData();
      this.setState((props,state) => {
        return ({downloadData: downloadData})
      })
    }
    changeState = () => {
      if (this.state.isExtended === "extended") {
        this.setState((props, state) => {
          return { isExtended: "not-extended" }
        })
      }
      else {
        this.setState((props, state) => {
          return { isExtended: "extended" }
        })
      }
    }
    changePopUp = (type = "none") => {
      this.setState((props, state) =>{
        return {isExtended: this.state.isExtended, popUp: !this.state.popUp, popUpType: type}
      })
    }
    popUp() {
      if (!this.state.popUp) {
        return;
      }

      return(<PopUp changePopUp={this.changePopUp} type={this.state.popUpType} downloadData={this.state.downloadData}/>)
    }
    render() {
      if (!isLoggedIn()) {
        return <Redirect to="/login" />
      }
      var loadingBarList = [1, 3, 2];
      var width = "50vw";
      return (
        <div>
          {this.popUp()}
          
          <div style={{ position: "fixed" }}><Navbar pageName="Home" sideNavAnimation={this.changeState} /></div>
          <div style={{ position: "fixed", zIndex: -1 }} className={this.state.isExtended}>
            
            <div style={{ display: "inline-block", position: "absolute" }}><SideNav isExtended={"extended"} /></div>
            <div className="video-outer-div">
              <div style={{ width: width.toString(), aspectRatio:"16/9" }}>
              <VideoPlayer />
              </div>
              <div style={{ width: width.toString }}>
                <div style={{ display: "inline-block" }}>
                  <h1 style={{ fontSize: "3vh" }}>Startup: Company inc.  </h1>
                  <h1 style={{ fontSize: "2vh" }}>Entrepreneur:   Name Nameson</h1>
                </div>
  
                <div style={{ display: "inline-block", float: "right" }}>
  
                  <div style={{ backgroundColor: "#E8E8E8", height: "13vh", width: "27vw", marginTop: "2vh", borderRadius: "2vh"}}>
                  <div style={{ justifyContent: "space-evenly", display: "flex" , transform: "translateY(1.5vh)"}}>
                    <button onClick= {() => this.changePopUp("download")} style={{cursor: "pointer"}}>
                    <MasterButton size="20px" loadingBarCompletion={loadingBarList[0]} text="Download"/> 
                    </button>
                    <button onClick= {() => this.changePopUp("challenge")} style={{cursor: "pointer"}}>
                    <MasterButton size="100px" loadingBarCompletion={loadingBarList[1]} text="Challenge"/> 
                    </button>
                    <button style={{cursor: "pointer"}}>
                    <MasterButton size="100px" loadingBarCompletion={loadingBarList[2]} text="Master"/>
                    </button>
                    
                    </div>
                  </div>
  
                </div></div>
              
            </div>
          </div>
          <div style={{position: "absolute", right:"0px", bottom:"0px", zIndex: "-20", height: "calc(90vh - 100px)", width: "calc(25vw - 50px)", borderRadius: "6vh 0 0 0", backgroundColor: "#f9f9f9"}}>
          <div style={{transform: "translateY(-70px)", 
        backgroundColor: "#f9f9f9", width: "200px", padding: "20px", borderRadius: "2vh"
        }}>XP:  <XPBar completion={user.xp}/>    Cash:  {user.cash}</div>
          <div style={{marginLeft: "20px", marginTop: "20px"}}>
            <p1>Placeholder text</p1>
          </div>

          </div>
        </div>
      )
    }
  }
  class XPBar extends React.Component {
    render() {
      var completion = (this.props.completion)*3/100
      return <div style={{width: "3vw", height: "13px", 
      backgroundColor: "white", display: "inline-block", borderRadius: "1vh"}}>
        <div style={{width: `${completion}vw`, height: "13px", 
      backgroundColor: "#8bee7c", borderRadius: "1vh"}}>

        </div>
      </div>
    }
  }
  class CloseIcon extends React.Component {
    render() {
      return(<svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 0 24 24" width="24px" fill="#000000"><path d="M0 0h24v24H0z" fill="none"/><path d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12z"/></svg>)
    }
  }
  class PopUp extends React.Component {

    render() {
      return <div style={{position: "absolute", zIndex: "100", width: "100vw", justifyContent: "center",
      alignItems: "center", height: "100vh", display: "flex"
    }} >
      <div style={{position: "absolute", width: "100vw", height: "100vh", backgroundColor: "black",
    opacity: "50%"
    }} onClick={this.props.changePopUp}>
      
    </div>
      {(this.props.type === "challenge") ? <ChallengePopUp changePopUp={this.props.changePopUp} downloadData={this.props.downloadData}/>: null}
      {(this.props.type === "download") ? <DownloadPopUp changePopUp={this.props.changePopUp} downloadData={this.props.downloadData}/>: null}
    </div>
    }
  }
  //<VideoInfo size="550px" />

  class ChallengePopUp extends React.Component {
    constructor(props) {
      super(props);
      this.state = {selectedFile: null
      }
    }
    onChangeHandler=event=>{
      this.setState({
        selectedFile: event.target.files[0],
        loaded: 0,
      })
    }
    renderChallenges() {
      var lst = this.props.downloadData.challenges
      return lst.map(text => (<div style={{marginBottom: "20px"}}>
        <CheckIcon /><p1 style={{marginLeft: "15px"}}>
        {text}</p1></div>))
    }
    render() {
      return(
        <div style={{height: "80vh", width: "25vw", backgroundColor: "white", opacity: "100%", zIndex: 2, borderRadius: "20px"}}>
      <button style={{cursor: "pointer",float: "right", marginTop: "15px", marginRight: "15px"}} onClick={this.props.changePopUp}
         
         ><CloseIcon /></button>
        <h1 style={{marginLeft: "20px"}}>Challenges</h1>
        <div style={{marginLeft: "20px", display: "block"}}>
        {this.renderChallenges()}
        
        
        <div style={{position: "absolute", bottom: "15vh"}}>
        <input type="file" name="file" onChange={this.onChangeHandler}/><button style={{backgroundColor: "#965594", 
        color: "white", marginLeft: "10px", padding: "7px", borderRadius: "5px", cursor: "pointer"}}
        onClick={this.props.changePopUp}
        >Submit</button>
        </div>
        </div>
        
      </div>
      )
    }
  }

  class DownloadPopUp extends React.Component {
    constructor(props) {
      super(props);
      this.state = {downloadData: this.props.downloadData}
    }
    renderDownload() {
      return [`${this.state.downloadData.download_title} points: ${this.state.downloadData.download_points}`].map(text => (<div style={{marginBottom: "20px"}}>
        <p1 style={{marginLeft: "15px"}}>
        {text}</p1></div>))
    }
    render() {
      return (<div style={{ width: "25vw", backgroundColor: "white", opacity: "100%", zIndex: 2, borderRadius: "20px", paddingBottom: "50px"}}>
      <button style={{cursor: "pointer",float: "right", marginTop: "15px", marginRight: "15px"}} onClick={this.props.changePopUp}>
        <CloseIcon /></button>
        <h1 style={{marginLeft: "20px"}}>Download</h1>
        {this.renderDownload()}
        <div style={{position: "absolute"}}>
        
        <a href={this.state.downloadData.download_url}><button style={{backgroundColor: "#965594", 
        color: "white", marginLeft: "10px", padding: "7px", borderRadius: "5px", cursor: "pointer"}}
        onClick={this.props.changePopUp}
        >Download Files</button></a>
        </div>
        
      </div>
      )
    }
  }

class VideoPlayer extends React.Component {
  constructor(props) {
    super(props)
    this.state = {video_url: "https://img.pikbest.com/58pic/35/39/61/62K58PICb88i68HEwVnm5_PIC2018.gif!w340"}
    this.refreshVideoPlayer()
  }
  refreshVideoPlayer = async () => {
    var newState = await loadDownloadData()
    this.setState((props, state) => {
      return newState})
  }
  render() {
    return (<>
      <iframe src={this.state.video_url}
    style={{width: "100%", height: "100%"}}
    frameborder='0'
    allow='autoplay; encrypted-media'
    allowfullscreen="true"
    title='video'
  />
    </>)
  }
}